.articles__table{
    list-style: none;
    padding: 0;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(284px, 1fr));
    gap: 10px;
    padding-left: 10px;
    padding-right: 10px;
}

@media (max-width: 768px){
    .articles__table {
        margin: 0 auto; 
    }
}